.flexboxes{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-left: 6%;
  /* margin: right 1px; */
  flex-wrap: wrap;
  justify-content:stretch;
}

.flexboxes img{
  height:auto;
  width:70%;
}

.flex1{
  flex:25%;
  
}
  .flex2{
    flex:50%;
}
.flex3{
  flex:75%;
}
  .flex4{
    flex:90%;
  }
 
@media (max-width: 1200px) {
  .flex1, .flex2, .flex3, .flex4{
    flex:100%;
    flex-direction: column;
  }
}
